import React from 'react';
import '../Styles/AboutUs.css';
import Bounce from 'react-reveal/Bounce';
import Typing from 'react-typing-animation';
import Footer from '../Footer';

export default function AboutUs() {
    return (
        <section className="aboutus-container">
            <Typing speed={100}>
            <h1 className="about-heading">Porte Bonheur SPA</h1>
            </Typing>
            <div className="vm-container">
            <Bounce left>
            <div className="vision">
                <h2>Vision</h2>
                <p>To see the world where people perform at their best to attain their primary purpose for living and enjoying the best quality of life by being their consistent first and best choice for high-quality, innovative, highly-satisfying, and affordable skin-care services and massage experience need provider.</p>
            </div>
            </Bounce>
            <Bounce right>
            <div className="mission">
                <h2>Mission</h2>
                <p>Our business is created to honor all people who work for their purpose for living by sincerely providing their need for high-quality, innovative, highly-satisfying, and affordable skin-care services and massage experience for a healthier mind, body, emotion, and spirit that enable people to perform at their best and achieve the best quality of life.</p>
            </div>
            </Bounce>
            </div>
            <Bounce bottom>
            <div className="owner-information">
                <h3 className='owner-heading'>The Management</h3>
                <p>The Porte Bonheur SPA is onwed and managed by Tabio family. </p>
            </div>
            </Bounce>
            <Footer/>
        </section>
    )
};
