import React from 'react';
import '../Styles/Home.css'
import cover from './Cover.png';
import Slide from 'react-reveal/Slide';
import Footer from '../Footer';
import massage from './pexels-elina-fairytale-3865804.jpg';
import facial from './pexels-gustavo-fring-3985338.jpg';

export default function Home() {
    return (
        <section className="home-section">
            <Slide bottom>
            <div className='card'>
                <img src={cover} alt="porte-bonheur-spa"/>
            </div>
            </Slide>
            <div className="small-cards-container">
                <div className="card1">
                    <div>
                    <img src={massage} alt="massage"/>
                    <h2 className="massage-heading">Massage</h2>
                    <p className="massage-text">We specialize in wellness or relaxation massage that promotes stress-relief and relaxation of the whole body and mind.</p>
                    </div>
                </div>
                <div className="card2">
                    <div>
                    <img src={facial} alt="mask-facial"/>
                    <h2 className="facial-heading">Skin</h2>
                    <p className="facial-text">We also offer safe skincare services like complete facial cleansing and diamond peel.</p>
                    </div>
                </div>
            </div>
            <Footer/>
        </section>
    )
}