import React from 'react';
import axios from 'axios';
import { useState } from 'react';
import { showErrMsg, showSuccessMsg } from './Notification';
import '../../Styles/Newletter.css';

const initialState = {
    firstname: '',
    lastname: '',
    email: '',
    err: '',
    success: ''
};

const initialUnsub = {
    existingEmail: '',
    fail: '',
    unSubscribed: ''
};

export default function Newsletter() {

    const [user, setUser] = useState(initialState);
    const [unsub, setUnsub] = useState(initialUnsub);

    const {firstname, lastname, email, err, success} = user;
    const {existingEmail, fail, unSubscribed} = unsub;

    const handleInput = (event) => {

        const {name, value} = event.target;

        setUser({...user, [name]:value, err: '', success: ''});
    };

    const handleUnsubInput = (event) => {
        setUnsub({...unsub, existingEmail: event.target.value, fail: '', unSubscribed: ''});
    };


    const handleSubmit = async (e) => {
        e.preventDefault();

        if(firstname.length === 0 || lastname.length === 0)
            return setUser({...user, err: 'Fill in all the required information', success: ''});
        
        if(email.length === 0)
            return setUser({...user, err: 'Enter a valid email address', success: ''});

        try {
            const res = await axios.post('/api/subscribe', {
                firstname,
                lastname,
                email
            });

            setUser({...user, err: '', success: res.data.msg});
        } catch (error) {
            error.response.data.msg && setUser({...user,err: error.response.data.msg, success: ''})
        }
    };

    const handleUnsub = async (e) => {        
        e.preventDefault();

        if(existingEmail.length === 0)
            return setUnsub({...unsub, fail: 'Enter a valid email address', unSubscribed: ''});

        try {
            const resp = await axios.delete('/api/unsubscribe', {data: {existingEmail}});

            setUnsub({...unsub, fail: '', unSubscribed: resp.data.msg});
        } catch (error) {
            error.response.data.msg && setUnsub({...unsub, fail: error.response.data.msg, unSubscribed: ''})
        }
    }

    return (
        <div className="form-container">
            <form className="subscribe-form">
                <h3 className="form-heading">Newsletter</h3>
                    {err && <div className='err-notification'>{showErrMsg(err)}</div>}
                    {success && <div className='success-notification'>{showSuccessMsg(success)}</div>}
                <div>
                    <input type='text' value={firstname} name="firstname" placeholder='First Name' onChange={handleInput}/>
                </div>
                <div>
                    <input type='text' value={lastname} name="lastname" placeholder='Last Name' onChange={handleInput}/>
                </div>
                <div>
                    <input type='text' value={email} name="email" placeholder='Your email' onChange={handleInput}/>
                </div>
                    <small>All the information you entered above are <br/>kept confidential.</small>
                <div>
                    <button onClick={handleSubmit}>Subscribe</button>
                </div>
            </form>
            <form className="unsubscribe-form">
                <h3 className="form-heading">Unsubscribe</h3>
                    {fail && <div className='err-notification'>{showErrMsg(fail)}</div>}
                    {unSubscribed && <div className='success-notification'>{showSuccessMsg(unSubscribed)}</div>}
                <div>
                    <input type='text' value={existingEmail} name="existingEmail" placeholder="Subscribed email" onChange={handleUnsubInput}/>
                </div>
                    <small>Enter the email address you used<br/>to subscribe to unsubscribe.</small>
                <div>
                    <button onClick={handleUnsub}>Unsubscribe</button>
                </div>
            </form>
        </div>
    )
};